












import Vue from "vue";
import { Input } from "ant-design-vue";

Vue.use(Input);

export default Vue.extend({
  name: "ControlPannel",
  data() {
    return {
      value: ""
    };
  },
  methods: {
    todoCreate() {
      this.$store.commit("add", this.value);
      this.value = "";
    }
  }
});
