






























import Vue from "vue";
import { Checkbox, List, Input, Modal } from "ant-design-vue";
import { store } from "../store";

Vue.use(Checkbox);
Vue.use(List);
Vue.use(Input);

export default Vue.extend({
  name: "Todo",
  data() {
    return {
      editMode: false,
      value: this.text
    };
  },
  props: {
    id: Number,
    completed: Boolean,
    text: String
  },
  methods: {
    todoCreate() {
      this.editMode = false;
      store.commit("updateText", { text: this.value, id: this.id });
    },
    todoComplete() {
      store.commit("complete", { id: this.id, completed: !this.completed });
    },
    confirmDelete() {
      const id = this.id;
      Modal.confirm({
        title: "Do you really want to remove the todo?",
        onOk() {
          store.commit("delete", id);
        }
      });
    }
  }
});
